import React from 'react'
import  apeHomeImg from '../../../assets/images/pages/projet/apecrosmieres__home.jpg' 
import  apeHomeMobileImg from '../../../assets/images/pages/projet/apecrosmieres__home--mobile.jpg' 
import  apeEventImg from '../../../assets/images/pages/projet/apecrosmieres__event.jpg' 
import  apeEventAddImg from '../../../assets/images/pages/projet/apecrosmieres__event--add.jpg' 
import  apeUserImg from '../../../assets/images/pages/projet/apecrosmieres__user.jpg' 
import { GoLinkExternal, GoMarkGithub } from "react-icons/go";


export default function Project2() {
  return (
    <section id="project2" className='project__description'>
            {/* <details> */}
        {/* <summary> */}
            <h2>APE Crosmires</h2>
            {/* </summary> */}

            <a href='https://apecrosmieres.fr/' target="_blank" rel="noreferrer" title='lien vers le site'><GoLinkExternal />Lien vers le site APE Crosmires</a>
            <a href='https://github.com/Christophe-Desmarres/APE-Crosmieres' target="_blank" rel="noreferrer" title='lien vers github front'><GoMarkGithub />Lien vers le repo frontend</a>
            <a href='https://github.com/Christophe-Desmarres/APE-Crosmieres-back' target="_blank" rel="noreferrer" title='lien vers github back'><GoMarkGithub />Lien vers le repo backend</a>
            <p>Projet ralis avec <span className='tag'>WordPress</span> customis pour la partie backend et avec <span className='tag'>Vue.js</span> et <span className='tag'>axios</span> pour le frontend</p>
            <p>
            But : fournir une plateforme de communication des venements et des ventes pour l'Amicale des Parents d'Eleves de Crosmires
            <br />
            Ralisation par une quipe de 5 personnes
            Mon rle : Product Owner
            <ul>
            <br />
            Participation :
                <li>- rdaction des user stories</li>
                <li>- rdaction des routes</li>
                <li>- rdaction MLD, MCD</li>
                <li>- rdaction des wireframes</li>
                <li>- rdaction du cahier des charges</li>
                <li>- configuration de la partie custom de Wordpress (plugin, endpoint, rles)</li>
                <li>- dveloppement vue et composants Vue.js</li>
            </ul>
                                
            <ul>
            <br />
                cot cient
                <li>- se crer un compte</li>
                <li>- modifier son profil</li>
                <li>- visualiser les vnements et ventes</li>
                <li>- s'inscrire ou commander sur l'venement</li>
            </ul>
            <ul>
            <br />
                cot administrateur
                <li>- CRUD venements et ventes</li>
                <li>- gestion des utilisateurs et des rles</li>
            </ul>
            </p>
            <div className='imgDiapo'>
                <p>Accueil version desktop</p>
                <img src={apeHomeImg} alt="cran d'accueil version bureau du projet apecrosmieres" />
                <p>Accueil version mobile</p>
                <img src={apeHomeMobileImg} alt="cran d'accueil version mobile du projet apecrosmieres" />
                <p>Dtail d'un venement</p>
                <img src={apeEventImg} alt="venement du projet apecrosmieres" />
                <p>Espace administration : cration d'un venement</p>
                <img src={apeEventAddImg} alt="cran de cration d'un venement du projet apecrosmieres" />
                <p>Espace administration : liste des utilisateurs</p>
                <img src={apeUserImg} alt="liste des utilisateurs de l'ecran d'administration du projet apecrosmieres" />
            </div>
            {/* </details> */}

    </section>  )
}
